/* Hover Effects */

hr {
  border: none;
  height: 20px;
  height: 50px;
  box-shadow: 0 20px 20px -20px #333;
  margin: -50px auto 10px;
}
.navbar-list li a:hover {
  color: gray;
  text-decoration: underline;
  text-underline-offset: 3px;
  transition: 0.3s all;
}

/* Media Queries */

@media (max-width: 768px) {
  .navbar-list {
    position: absolute;
    top: 55px;
    left: 0;
    width: 100%;
    background-color: #f2f2f2;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    transform: translateX(100%);
    box-shadow: 0 4px 6px -1px rgb(0 0 0 / 0.85),
      0 2px 4px -2px rgb(0 0 0 / 0.1);
  }
  .active .navbar-list {
    transform: translateX(0%);
    z-index: 9999;
  }

  .active .open {
    display: none;
  }

  .active .close {
    display: block;
  }
}
